import computeLlmImpacts from "@genai-impact/ecologits.js/es/impacts/dag.js";
import { DEFAULT_IMPACT } from "@genai-impact/ecologits.js/es/impacts/default.js";
import { log4TSProvider } from "./config/ConfigLog4j";
const log = log4TSProvider.getLogger("ecologits-wrapper.ts");
const url = "https://raw.githubusercontent.com/genai-impact/ecologits/refs/heads/main/ecologits/data/models.json";
class EcoLogitsData {
    /**
   * The static getter that controls access to the singleton instance.
   *
   * This implementation allows you to extend the Singleton class while
   * keeping just one instance of each subclass around.
   */
    static get instance() {
        if (!EcoLogitsData.ecologitsData) {
            log.debug("EcoLogitsData: starting to fetch data !");
            fetch(url).then((res) => {
                log.debug("EcoLogitsData: fetch answer !");
                return res.json().then((json) => {
                    // On extrait les modles du champ 'models' du JSON
                    const models = (json.models || []).map((model) => {
                        // Certains champs peuvent tre absents ou sous un autre format, on adapte
                        let totalParameters = [];
                        let activeParameters = [];
                        if (model.architecture && typeof model.architecture.parameters === 'object') {
                            // MoE: { total, active }
                            if (model.architecture.parameters.total !== undefined) {
                                totalParameters = [parseFloat(model.architecture.parameters.total)];
                            }
                            if (model.architecture.parameters.active !== undefined) {
                                if (typeof model.architecture.parameters.active === 'object') {
                                    // active: { min, max }
                                    if (model.architecture.parameters.active.min !== undefined && model.architecture.parameters.active.max !== undefined) {
                                        activeParameters = [parseFloat(model.architecture.parameters.active.min), parseFloat(model.architecture.parameters.active.max)];
                                    }
                                }
                                else {
                                    activeParameters = [parseFloat(model.architecture.parameters.active)];
                                }
                            }
                        }
                        else if (model.architecture && typeof model.architecture.parameters === 'number') {
                            totalParameters = [parseFloat(model.architecture.parameters)];
                            activeParameters = [parseFloat(model.architecture.parameters)];
                        }
                        return {
                            provider: model.provider,
                            name: model.name,
                            totalParameters,
                            activeParameters,
                            warnings: Array.isArray(model.warnings) ? model.warnings.join('; ') : (model.warnings || ''),
                            sources: Array.isArray(model.sources) ? model.sources.join('; ') : (model.sources || ''),
                        };
                    });
                    this.ecologitsData = new EcoLogitsData(models);
                });
            });
        }
        return this.ecologitsData;
    }
    constructor(data) {
        this.data = [];
        log.debug("building EcologitsData object with " + data.length + " models.");
        if (data.length === undefined) {
            throw new Error("Cannot be called directly. Use build() instead.");
        }
        chrome.storage.local.set({ ecologitsData: data });
        this.data = data;
    }
    findModel(provider, name) {
        return this.data.find((model) => model.provider === provider && model.name === name);
    }
    average(arr) {
        return arr.reduce((a, b) => a + b, 0) / arr.length;
    }
    computeLlmImpacts(provider, modelName, outputTokenCount, requestLatency) {
        const model = this.findModel(provider, modelName);
        if (!model) {
            return DEFAULT_IMPACT;
        }
        const modelActiveParams = this.average(model.activeParameters);
        const modelTotalParams = this.average(model.totalParameters);
        return computeLlmImpacts(modelActiveParams, modelTotalParams, outputTokenCount, requestLatency);
    }
}
EcoLogitsData.calculateAverage = (array) => {
    const sum = array.reduce((a, b) => a + b);
    return sum / array.length;
};
export default EcoLogitsData;
